.form-control input {
  display: block;
  width: 98%;
  border: 1px solid #ccc;
  font: inherit;
  line-height: 1.5rem;
  padding: 0 0.25rem;
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin: 1rem auto;
}

.form-control input:focus {
  outline: #0acddb;
  border: 1px solid #0acddb;
}
